    <div class="gradeArea">
      <div class="grades">
        <div
          :class="{
            [`g${largeLevel}${
              levelModel >= 1 || levelModel == 0 ? 'l' : ''
            }`]: true,
          }"
          class="grade"
        />
        <div
          :class="{
            [`g${largeLevel}${
              levelModel >= 2 || levelModel == 0 ? 'l' : ''
            }`]: true,
          }"
          class="grade"
        />
        <div
          :class="{
            [`g${largeLevel}${
              levelModel >= 3 || levelModel == 0 ? 'l' : ''
            }`]: true,
          }"
          class="grade"
        />
        <div
          :class="{
            [`g${largeLevel}${
              levelModel >= 4 || levelModel == 0 ? 'l' : ''
            }`]: true,
          }"
          class="grade"
        />
        <div
          :class="{ [`g${largeLevel}${levelModel == 0 ? 'l' : ''}`]: true }"
          class="grade"
        />
      </div>
      <van-progress
        :percentage="(credit.currentScore / credit.nextUpgradeScore) * 100"
        :show-pivot="false"
        color="#6F5BFEFF"
      />
      <div class="subtitle">
        <div class="left">{{ credit.currentScore }}</div>
        <div class="center">距离升级还差{{ credit.diffScore }}分</div>
        <div class="right">{{ credit.nextUpgradeScore }}</div>
      </div>
    </div>
    <div class="content">
      <div class="title">一、信用等级介绍</div>
      <div class="content">
