        <p>
          1）{{$root.title}}信用等级体系包含五个大等级，每个大等级里分为五个等级，每个大等级分别显示不同的标志；具体如下图所示：
        </p>
        <div class="grades">
          <div class="item">
            <div class="grade g1l"></div>
            <div>升级标准</div>
            <div>1分</div>
          </div>
          <div class="item">
            <div class="grade g2l"></div>
            <div>升级标准</div>
            <div>10000分</div>
          </div>
          <div class="item">
            <div class="grade g3l"></div>
            <div>升级标准</div>
            <div>60000分</div>
          </div>
          <div class="item">
            <div class="grade g4l"></div>
            <div>升级标准</div>
            <div>160000分</div>
          </div>
          <div class="item">
            <div class="grade g5l"></div>
            <div>升级标准</div>
            <div>310000分</div>
          </div>
        </div>
        <p>
          2）信用等级由信用等级分值决定，信用等级分值越高，信用等级越高，可享受的权益也就越多；
        </p>
        <p>
          3）信用等级分根据您在{{$root.title}}平台的个人账户的买卖V币情况，信誉记录，使用{{$root.title}}平台在合作三方充值提款金额、次数等情况，来综合计算得出的相应分值；
        </p>
        <p>2、各等级对应成长值</p>
        <p>
          信用等级分值决定信用等级，信用等级分值达到途中所示升级标准，即可升级为对应的等级。
        </p>
        <div class="examples">
          <div class="group">
            <div
              v-for="(item, index) in examplesData"
              :key="index"
              :class="{ paddingTop: index >= 5 && index % 5 == 0 }"
              class="row"
            >
              <div class="label">{{ item.label }}</div>
              <div class="grades">
                <div
                  v-for="index in 5"
                  :key="index"
                  :class="`g${item.grade}${item.level >= index ? 'l' : ''}`"
                  class="grade"
                ></div>
