      </div>
      <div class="title">二、如何升级</div>
      <div class="content">
        <p>
          购买、出售：在{{$root.title}}平台进行购买以及出售V币行为，且保持良好的交易记录，即可获得信用积分；
        </p>
        <p>
          充值、提款：使用{{$root.title}}充值我们合作商户或从合作商户提款至{{$root.title}}钱包根据充提数值可获得相应信用积分。
        </p>
      </div>
      <div class="title">三、其他说明</div>
      <div class="content">
        <p>
          {{$root.title}}平台可在法律允许的范围内对规则进行变动调整，相关的变动和调整将公布在说明页面上，并与公布时即时生效。以上，最终解释权，归{{$root.title}}平台所有。
        </p>
      </div>
    </div>
  </div>
