<script>
import {getCreditInfo, getCreditLevelConfig} from "@/apis/user";

export default {
  data() {
    return {
      credit: {
        // creditLevelId: 1,
        // currentScore: 0,
        // diffScore: 100,
        // icon: "1",
        // level: 1,
        // name: "信用1级",
        // nextUpgradeScore: 100,
        // upgradeScore: 0,
        creditLevel: {
          creditLevelId: 1,
          icon: "",
          level: 1,
          name: "信用1级",
          upgradeScore: 0,
        },
        currentScore: 1,
        diffScore: 0,
        nextUpgradeScore: 1,
      },
      examplesData: [
        {
          label: "1分~1999分",
          level: 1,
          grade: 1,
        },
        {
          label: "2000分~3999分",
          level: 2,
          grade: 1,
        },
        {
          label: "4000分~5999分",
          level: 3,
          grade: 1,
        },
        {
          label: "6000分~7999分",
          level: 4,
          grade: 1,
        },
        {
          label: "8000分~9999分",
          level: 5,
          grade: 1,
        },

        {
          label: "10000分~19999分",
          level: 1,
          grade: 2,
        },
        {
          label: "20000分~29999分",
          level: 2,
          grade: 2,
        },
        {
          label: "30000分~39999分",
          level: 3,
          grade: 2,
        },
        {
          label: "40000分~49999分",
          level: 4,
          grade: 2,
        },
        {
          label: "50000分~59999分",
          level: 5,
          grade: 2,
        },
        {
          label: "60000分~79999分",
          level: 1,
          grade: 3,
        },
        {
          label: "80000分~99999分",
          level: 2,
          grade: 3,
        },
        {
          label: "100000分~119999分",
          level: 3,
          grade: 3,
        },
        {
          label: "120000分~139999分",
          level: 4,
          grade: 3,
        },
        {
          label: "140000分~159999分",
          level: 5,
          grade: 3,
        },
        {
          label: "160000分~189999分",
          level: 1,
          grade: 4,
        },
        {
          label: "190000分~219999分",
          level: 2,
          grade: 4,
        },
        {
          label: "220000分~249999分",
          level: 3,
          grade: 4,
        },
        {
          label: "250000分~279999分",
          level: 4,
          grade: 4,
        },
        {
          label: "280000分~309999分",
          level: 5,
          grade: 4,
        },
        {
          label: "310000分~379999分",
          level: 1,
          grade: 5,
        },
        {
          label: "380000分~409999分",
          level: 2,
          grade: 5,
        },
        {
          label: "420000分~459999分",
          level: 3,
          grade: 5,
        },
        {
          label: "460000分~499999分",
          level: 4,
          grade: 5,
        },
        {
          label: "500000分以上",
          level: 5,
          grade: 5,
        },
      ],
    };
  },
  computed: {
    levelModel() {
      return this.credit.creditLevel.level % 5;
    },
    largeLevel() {
      if (this.levelModel >= 1) {
        return (this.credit.creditLevel.level - this.levelModel) / 5 + 1;
      } else {
        return this.credit.creditLevel.level / 5;
      }
    },
  },
  created() {
    this.$root.$emit("root:set-topbar", ["left-back"]);
    this.loadUserCreditInfo();
    this.loadCreditLevelConfig();
  },
  methods: {
    loadUserCreditInfo() {
      getCreditInfo().then(({ok, data, msg}) => {
        if (ok) {
          this.credit = data;
        } else {
          this.$toast.fail(msg);
        }
      });
    },
    loadCreditLevelConfig() {
      getCreditLevelConfig().then(({data, ok, msg}) => {
        if (ok) {
          this.examplesData.map((item, index) => {
            const current = data[index];
            const next = data[index + 1];
            if (next) {
              item.label = `${current.upgradeScore}分~${next.upgradeScore}分`;
            } else {
              item.label = `${current.upgradeScore}分以上`;
            }
            return item;
          });
        } else {
          this.$toast.fail(msg);
        }
      });
    },
  },
};
</script>

